.form-select {
  position: relative;
  display: flex;

  &__label {
    width: 100%;
    background-image: url("../../img/svg/loupe.svg");
    background-repeat: no-repeat;
    background-position: center right;
  }

  &__field {
    @include fz(16px);
    width: 100%;
    padding: 10px 0;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    border: none;
    border-bottom: 2px solid rgba(187, 187, 187, 0.5);
    background-color: transparent;
  }

  &__head {
    @include fz(15px);
    display: inline-block;
    min-width: 142px;
    padding: 0 50px 0 10px;
    color: $c-blue;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-align: left;
    background-image: url("../img/svg/arrow-bottom.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center right 2px;
    background-size: 10px;
    border: none;
    cursor: pointer;

    &.arrow-up {
      background-image: url("../img/svg/arrow-up.svg");
    }
  }

  &__list {
    position: absolute;
    z-index: 3;
    top: calc(100% + 10px);
    right: 0;
    display: none;
    width: 200px;
    padding: 10px 0;
    margin: 0;
    background: $c-white;
    border-radius: 10px;
    box-shadow: 0 20px 45px rgba(0, 0, 0, .2);

    &_search {
      width: 100%;
      max-height: 350px;
      overflow: auto;
    }

    &.open {
      display: block;
    }
  }

  &__item {
    list-style: none;

    a {
      @include fz(16px);
      display: block;
      padding: 10px 30px 8px;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      text-decoration: none;

      &:hover {
        background-color: #F3F3F3;
      }
    }
  }
}