.pagination {
  @include mq(tablet) {
    margin: 0 0 60px;
  }

  &-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;

    &__item {
      display: flex;
      margin: 0 2px;

      &:first-child {
        justify-content: flex-start;
        flex-grow: 1;
      }

      &:last-child {
        justify-content: flex-end;
        flex-grow: 1;
      }
    }
  }

  &__wrap-btn {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      background-color: rgba(147, 182, 227, .3);

      @include mq(tablet) {
        height: 60px;
        width: 60px;
      }
    }
  }

  &__btn {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    border: none;
    border-radius: 50%;
    background-color: #F3F3F3;
    cursor: pointer;
    transition: transform .2s;
    overflow: hidden;

    @include mq(tablet) {
      height: 60px;
      width: 60px;
    }

    svg {
      height: 16px;
      width: 10px;
      fill: #B6B6B6;
    }

    &:hover {
      background: linear-gradient(45deg, #3c589e, #93b7e4);

      svg {
        fill: $c-white;
      }
    }

    &_prev:hover {
      transform: translateX(-16px);
    }

    &_next:hover {
      transform: translateX(16px);
    }
  }
  
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    @include fz(16px);
    color: #B9B9B9;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;

    &:not(.active):hover {
      color: $c-link-hover;
    }

    &.active {
      color: $c-blue;
    }
  }
}