.slider {
  &__row {
    display: flex;
    justify-content: space-between;
    margin: 0 0 30px;
    padding: 0 0 10px;
    border-bottom: 2px solid rgba(187, 187, 187, .5);
  }

  &__field {
    @include fz(16px);
    max-width: 100px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    border: none;
    outline: none;

    &.right {
      text-align: right;
    }
  }

  &__icon {
    width: 30px;

    svg {
      width: 100%;
      height: 100%;
      fill: #08396f;
    }
  }

  .ui-slider {
    width: calc(100% - 18px);
    margin: auto;
    height: 6px;
    background: #E9E9E9;
    border-radius: 4px;
    border: none;

    .ui-slider-range {
      background: linear-gradient(45deg, #3c589e, #93b7e4);
    }
  }

  .ui-slider-handle {
    z-index: 1;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 4px solid $c-white;
    box-shadow: 0 0 5px 5px rgba(139, 173, 220, .4);
    background: linear-gradient(45deg, #3c589e, #93b7e4);
    cursor: pointer;
  }
}